@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap");

* {
  font-family: "Roboto", sans-serif;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.header {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  width: 100%;
  height: 70px;
  padding: 0 30px;
  border-bottom: 1px solid #dddddd;
}

.content {
    padding-top: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    width: 100%;
    padding-left: 30px;
    padding-right: 30px;
    margin: 0 auto;
}

.movie {
    cursor: pointer;
    width: 200px;
    height: 300px;
    margin: 5px;
    border: 1px solid #dddddd;
    border-radius: 3px;
}

.movie:hover {
    box-shadow: 3px 3px 7px #dddddd;
}

.movie_image {
    width: 100%;
    height: 150px;
    background-color: cornflowerblue;
}

.movie_info {
    width: 100%;
    height: 30px;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    text-align: right;
    padding: 0 5px;
}

.movie_info .green {
    color: #ffffff;
    padding: 2.5px 5px;
    border-radius: 3px;
    background-color: rgb(28, 204, 28);
}

.movie_info .orange {
    color: #ffffff;
    padding: 2.5px 5px;
    border-radius: 3px;
    background-color: rgb(204, 134, 28);
}

.movie_info .red {
    color: #ffffff;
    padding: 2.5px 5px;
    border-radius: 3px;
    background-color: rgb(204, 28, 28);
}

.movie_info h3 {
    text-align: left;
}

.overview {
    font-size: 12px;
    text-align: justify;
    padding: 0 5px;
}

.overview h3 {
    padding-top: 10px;
    padding-bottom: 5px;
}

@media (max-width: 480px) {
    .content {
        padding: 30px 25px;
    }
}

@media (max-width: 470px) {
    .content {
        padding: 30px 20px;
    }
}

@media (max-width: 320px) {
    .header {
        justify-content: center;
    }
    .content {
        justify-content: center;
    }
}